.titles {
    text-align: center;
    color: white;
    font-family: 'Courier New', monospace;
    font-weight: bold;
    text-decoration: underline;
    margin-top: 100px;
  }

  .techs {
    height: 100px;
    width: 100px;
    margin: 150px;
    
  }

  .container {
    width: 80%;
    height: 600px;
    left: 50%;
    top: 50%;
    margin-left: -20px;
    margin-top: 20px;
  }

  .scene {
    position: relative;
    width: 500px;
    height: 500px;
    margin: calc(100px / 2 + 0px);
  }

  
  .scene .techs {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100px;
    height: 100px;
    margin: calc(-100px / 2);

    border-radius: 50%;
  }
  

  .techs.react {
    top: 50px;
    left: 240px;
  }

  .techs.css {
    top: -80px;
    left: 270px;
  }

  .techs.bootstrap {
    height: 75px;
    width: 75px;
    top: 60px;
    left: 450px;
  }

  .techs.html {
    height: 155px;
    top: -95px;
    left: 475px;
    
  }

  .techs.js {
    top: -80px;
    left: 75px;
  }

  .techs.node {
    top: 40px;
